import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	data(){
		return{
			located:false,
			active:1,
			sh:'',
			dataList:[],
			page:1,
			pageSize:5,
			total:0,
			more:'',
			lat:'',
			lng:''
		}
	},
	components:{
		uniRate,
		uniLoadMore
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   mounted(){
	   	var that=this
	   	this.apart()
	   	setTimeout(function(){
	   		that.$getHeight('#sv',(res) =>{
	   			that.sh=res
	   		})
	   	},0)
	   },
	methods:{
		toShop(id){
			uni.navigateTo({
				url:`/pages/bussiness/shopPreview?id=${id}`
			})
		},
		toDetail(id){
			uni.navigateTo({
				url:`/pages/index/goodsDetail?id=${id}`
			})
		},
		
		toggle(t){
			this.active=t
			this.reset()
			this.apart()
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		apart(){
			var that=this,params={
					page:this.page,
					pagesize:this.pageSize
				},url
			if(this.active==1){
				 url='&r=api.discovery.merchant'
				if(this.located){
					params.lng=this.longitude
					params.lat=this.latitude
					this.getList(params,url)
				}else{
					this.locate()
				}
			}else{
				 url='&r=api.discovery.goods'
				that.getList(params,url)
			}
		},
		getList(p,url){
			var that=this
			if(this.page==1){
				this.$loading()
			}
			  this.$apiPost(url,p).then((res) =>{
				  that.dataList=that.dataList.concat(res.data)
				  that.total=res.total
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		locate(){
			uni.getLocation({
				type: 'wgs84',
				success:(res) =>{
					that.located=true
					that.lng=res.longitude
					that.lat=res.latitude
					params.lng=res.longitude
					params.lat=res.latitude
					that.getList(params,url)
				},
				fail:(reason) =>{
					params.lng=''
					params.lat=''
					that.getList(params,url)
					that.located=false
					that.$msg('请打开定位功能')
				}
			})
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.apart()
		  // },2000)
		},
	}
}
